@charset "utf-8";
@import url('https://fonts.googleapis.com/css?family=Cabin');

:root{
	/* Font families */
	--fallback-font-family: Helvetica, Arial, "Hiragino Sans GB", 
	"Hiragino Sans GB W3", Microsoft JhengHei, WenQuanYi Micro Hei, "Microsoft YaHei", sans-serif;
	--base-font-family: "Cabin", var(--fallback-font-family);
	--code-font-family: "inconsolata", Consolas, monospace, sans-serif;

	/* Font configuration */
	--base-font-size:   10pt;
	--small-font-size:  9pt;
	--big-font-size: 12pt;
	--base-line-height: 1.75em;

	/* Space unit */
	--space-s: 12px;
	--space-ms:18px;
	--space-m: 24px;
	--space-ml:30px;
	--space-l: 84px;

	/* Material color scheme */
	--material-red: #f44336;
	--material-pink: #E91E63;
	--material-purple: #9c27b0;
	--material-deep-purple: #673ab7;
	--material-indigo: #3f51b5;
	--material-blue: #2196f3;
	--material-teal: #009688;
	--material-green: #4caf50;
	--material-light-green: #8bc34a;
	--material-orange: #ff9800;
	--material-deep-orange: #ff5722;
	--material-brown: #795548;
	--material-blue-grey: #607d8b;
	--material-grey: #9e9e9e;
	--material-yellow: #FFEB3B;

	/* Social Networks Brand color */
	--github-color:     #467cc2;
	--twitter-color:    #55acee;
	--facebook-color:   #3765a3;
	--google-color:     #db4437;
	--pinterest-color:  #bc1725;
	--dribbble-color:   #ed699c;
	--weibo-color:      #f8712a;
	--tumblr-color:     #35465c;
	--instagram-color:  #3f729b;
	--linkedin-color:   #0077B5;
	--behance-color:    #1769FF;
	--tixora-color:     #33475A;

	/* Colors */
	--black: #404040;
	--white: #ffffff;
	--background-color: #F8F7F4;
	--brand-color:      #7EA634;
	--accent-color:     var(--material-green);
	--selection-color:  #AFE9B1;
	--grey-color:       #b3b3b3;

	/* Responsive limits */
	--min-sm:        768px;
	--min-md:        992px;
	--min-lg:       1200px;
}

*{
	margin: 0; padding: 0; 
	border: none;
	background: transparent;
}

*:focus {
    outline: none;
}

img{
	display: block;
}

a{
	text-decoration: none;
	color: var(--black);
}

a:visited{
	color: var(--black);
	-webkit-transition: color 100ms ease-in;
	-o-transition: color 100ms ease-in;
	transition: color 100ms ease-in;
}

@import './normalize.css';
@import './grid12.css';
@import './markdown.css';
@import './musicplayer.css';
@import './layout.css';



