
.musicplayer {
	position: absolute;
	width: 299px;
	bottom: var(--space-l);
}

.amplitude-prev:hover, .amplitude-next:hover, .amplitude-play-pause:hover {
	color: var(--accent-color);
	-webkit-transition: color 100ms ease-in;
	-o-transition: color 100ms ease-in;
	transition: color 100ms ease-in;
}

.amplitude-song-played-progress{
	display: -ms-inline-flexbox;
	display: inline-flex;
	width: 72%;
	height: 8px;
	margin-right: 4%;
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
}

.amplitude-song-played-progress::-webkit-progress-bar{
	background-color: transparent;
	border: 1px solid var(--black);
}

.amplitude-song-played-progress::-webkit-progress-value{
	background-color: var(--black);
}

.amplitude-prev, .amplitude-next, .amplitude-play-pause {
	display: -ms-inline-flexbox;
	display: inline-flex;
	text-align: center;
	vertical-align: -8%;
	width: 8%;
}


