.markdown{
	border: none
}

.markdown>p, 
.markdown>ul, 
.markdown>ol,
.markdown>h1,
.markdown>h2,
.markdown>h3,
.markdown>h4,
.markdown>h5,
.markdown>h6,
.markdown>blockquote{
	margin-bottom: calc(var(--base-line-height) - 1em);
}

.markdown>hr{
	margin: 1.25em 0;
}

.markdown>ul,
.markdown>ol{
	padding-left: 2em;
}

.markdown>blockquote{
	border: 1px solid var(--grey-color);
	border-radius: 4px;
	background-color: var(--white);
	font-family: Georgia;
	font-size: var(--big-font-size);
	padding: 0.5em 0.75em;
}

.markdown>hr{
	height: 1px;
	background-color: var(--grey-color);
}

.markdown>*>a {
    color: var(--accent-color);
}


